.related-products {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding-bottom: 50px;
}

.related-products h1 {
    color: #171717;
    /* font-size: 50px; */
    font-weight: 600;
}

.related-products hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: #252525;
}

.related-products-items {
    margin-top: 30px;
    display: flex;
    gap: 30px;
}

/* --- Responsiveness --- */
@media(max-width:1280px) {
    .related-products {
        height: 70vh;
    }
    .related-products h1 {
        font-size: 40px;
    }
    .related-products hr {
        width: 160px;
        height: 4px;
    }
    .related-products-items {
        gap: 20px;
        margin-top: 30px;
    }
}
@media(max-width:1024px) {
    .related-products {
        height: 80vh;
    }
    .related-products h1 {
        font-size: 30px;
    }
    .related-products hr {
        width: 120px;
        height: 3px;
    }
    .related-products-items {
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width:800px) {
    .related-products {
        height: 65vh;
        gap: 6px;
    }
    .related-products h1 {
        font-size: 20px;
    }
    .related-products hr {
        width: 100px;
    }
    .related-products-items {
        gap: 5px;
    }
}
@media(max-width:500px) {
    .related-products {
        height: 100%;
    }
    .related-products-items {
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 20px;
        column-gap: 20px;
    }
}