.loginsignup {
    /* border: 3px solid red; */
    width: 100%;
    height: 80vh;
    background: #FCE3FE;
    padding-top: 50px;
    padding-bottom: 50px;
}

.loginsignup-container {
    /* border: 3px solid red; */
    width: 580px;
    /* height: 650px; */
    background-color: white;
    margin: auto;
    padding: 40px 60px;
}

.loginsignup-container h1 {
    margin: 20px 0px;
}

.loginsignup-fields {
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}

.loginsignup-fields input {
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}

.loginsignup-container button {
    width: 100%;
    height: 72px;
    color: white;
    background-color: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.loginsignup-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

.loginsignup-login span {
    color: #ff4141;
    font-weight: 600;
}

.loginsignup-agree {
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 15px;
    font-weight: 500;
}

/* --- Responsive --- */
@media(max-width:1440px){
    .loginsignup {
        /* border: 1px solid red; */
        height: 100vh;
    }
  .loginsignup-container{
    width: auto;
    max-width: 500px;
    padding: 30px 45px;
    /* border: 1px solid red; */
  }
  .loginsignup-container h1 {
    margin: 10px 0;
  }
  .loginsignup-fields{
    gap: 20px;
    margin-top: 20px;
  }
  .loginsignup-fields input{
    height: 55px;
  }
 .loginsignup-login{
    font-size: 16px;
 }
 .loginsignup-agree{
    font-size: 13px;
    gap: 10px;
 }
 .loginsignup-container button {
    height: 52px;
    margin-top: 20px;
    font-size: 20px;
    font-weight: 500;
 }
}
@media(max-width:800px){
    .loginsignup-container{
        padding: 20px 30px;
    }
    .loginsignup-container h1 {
        font-size: 24px;
    }
    .loginsignup-fields{
        margin-top: 20px;
    }
    .loginsignup-fields input{
        height: 45px;
    }
    .loginsignup-container button {
        height: 45px;
        font-size: 16px;
     }
}