.productdisplay {
    display: flex;
    margin: 0 170px;
    /* border: 3px solid blue; */
}
.productdisplay-left {
    display: flex;
    gap: 17px;
}
.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-main-img {
    width: 586px;
    height: 700px;
}

/* css for productdisplay right */
.productdisplay-right {
    display: flex;
    flex-direction: column;
    margin: 0 100px;
}
.productdisplay-right h1 {
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 600;
    line-height: 1.3;
}
.productdisplay-right-stars {
    display: flex;
    align-items: center;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
    margin-top: 13px;
}
.productdisplay-right-prices {
    display: flex;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
    margin: 40px 0px;
}
.productdisplay-right-price-old {
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new {
    color: #ff4141;
}

.productdisplay-right-size  h1 {
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}

.productdisplay-right-sizes {
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}

.productdisplay-right-sizes div {
    padding: 18px 24px;
    background-color: #fbfbfb;
    border: 1px solid#ebebeb;
    border-radius: 3px;
    cursor: pointer;
}

.productdisplay-right button {
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background-color: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}

.productdisplay-right-category {
    margin-top: 8px;
}
.productdisplay-right-category span {
    font-weight: 600;
}

/* --- Responsive --- */
@media(max-width:1680px){
    .productdisplay{
        margin: 0 130px;
    }
    .productdisplay-right{
        margin: 0 80px;
    }
}
@media(max-width:1440px){
    .productdisplay{
        margin: 0 100px;
    }
    .productdisplay-left{
        gap: 10px;
    }
    .productdisplay-img-list{
        gap: 10px;
    }
    .productdisplay-img-list img{
        height: 120px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 510px;
    }
    .productdisplay-right{
        margin: 0 50px;
    }
    .productdisplay-right h1 {
        font-size: 22px;
    }
    .productdisplay-right-stars{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-right-stars p{
        font-size: 16px;
        margin-left: 5px;
        margin-top: 3px;
    }
    .productdisplay-right-prices{
        margin: 10px 0;
        font-size: 22px;
    }
    .productdisplay-right-description{
        font-size: 14px;
    }
    .productdisplay-right-size h1{
        margin-top: 20px;
        font-size: 20px;
    }
    .productdisplay-right-sizes {
        margin-top: 25px;
    }
    .productdisplay-right-sizes div{
        padding: 14px 20px;
    }
    .productdisplay-right button{
        width: 150px;
        padding: 15px 0;
        margin-bottom: 20px;
    }
    .productdisplay-right-category {
        margin-top: 5px;
    }
}
@media(max-width:1280px){
    .productdisplay-right{
        margin: 0 30px;
    }
    .productdisplay-right-prices{
        font-size: 18px;
    }
    .productdisplay-right-description{
        font-size: 13px;
    }
    .productdisplay-right-category {
        margin-top: 5px;
        font-size: 14px;
    }
}
@media(max-width:1024px){
    .productdisplay{
        margin: 0 30px;
    }
    .productdisplay-left{
        gap: 5px;
    }
    .productdisplay-img-list img{
        height: 80px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 350px;
    }
    .productdisplay-right h1 {
        font-size: 18px;
    }
    .productdisplay-right-stars img{
        width: 15px;
    }
    .productdisplay-right-description{
        font-size: 12px;
    }
    .productdisplay-right-sizes {
        margin-top: 10px 0;
    }
    .productdisplay-right-sizes div{
        padding: 10px 16px;
        font-size: 14px;
    }
    .productdisplay-right button{
        width: 120px;
        padding: 10px 0;
        margin-bottom: 10px;
        font-size: 14px;
    }
    .productdisplay-right-category {
        font-size: 12px;
    }
}
@media(max-width:800px){
    .productdisplay{
        margin: 0 20px;
    }
    .productdisplay-left{
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
        /* border: 3px solid blue; */
        padding-top: 0;
        justify-content: flex-end;
    }
    .productdisplay-img-list{
        flex-direction: row;
        gap: 6px;
    }
    .productdisplay-img-list img{
        height: 70px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 300px;
    }
    .productdisplay-right h1 {
        font-size: 14px;
    }
    .productdisplay-right-stars img{
        width: 13px;
    }
    .productdisplay-right-description{
        font-size: 10px;
    }
    .productdisplay-right-size h1{
        margin-top: 10px 0;
    }
    .productdisplay-right-sizes {
        margin-top: 10px;
        gap: 5px;
    }
    .productdisplay-right-sizes div{
        padding: 4px 11px;
    }
    .productdisplay-right button{
        width: 100px;
        font-size: 12px;
    }
    .productdisplay-right-category {
        font-size: 10px;
    }
}
@media(max-width:500px){
    .productdisplay{
        margin: 0 10px;
        flex-direction: column;
    }
    .productdisplay-left{
        gap: 10px;
        flex-direction: row;
        margin: auto;
        align-items: flex-start;
    }
    .productdisplay-img-list{
        flex-direction: column;
    }

    .productdisplay-right{
        margin: 5px;
    }
    .productdisplay-right h1{
        margin-top: 15px;
        font-size: 20px;
        font-weight: 500;
    }
    .productdisplay-right-stars img{
        width: 15px;
    }
    .productdisplay-right-sizes{
        gap: 10px;
        margin: 20px 0;
    }
    .productdisplay-right-sizes div{
        padding: 10px 16px;
    }
    .productdisplay-right button{
        width: 130px;
        font-size: 15px;
        padding: 12px 0;
    }
    .productdisplay-right-category{
        font-size: 16px;
    }
}